<template>
<div>
    <el-menu :default-active="$route.name" class="el-menu-vertical-demo" @select="handleSelectMenu" background-color="#324157" text-color="#fff">
        <el-menu-item v-for="(menu,idx) in aMenuList" :key="idx" :index="menu.name">
            <!-- <i :class="menu.icon"></i> -->
            <span slot="title">{{menu.title}}</span>
        </el-menu-item>
    </el-menu>
</div>
</template>

<script>
export default {
    name: 'sideMenu',
    components: {},
    props: {},
    data() {
        return {}
    },
    computed: {
        aMenuList() {
            return this.$store.state.app.aMenuList
        }
    },
    methods: {
        //点击菜单
        handleSelectMenu(params) {
            this.$router.push({
                name: params
            })
        },
    },
    mounted() {}
}
</script>

